@media (min-width: 230px) and (max-width: 480px) {
  .hero__popup {
    width: calc(100% - 80px);
  }
  .hero__popup--alert {
    top: 20%;
    left: 50%;
    transform: translate(-50%);
    font-size: 2rem;
  }
  .hero__popup--alertInfo {
    top: 55%;
    left: 10%;
  }
  .hero__popup--icon {
    left: 50%;
    top: 10%;
    font-size: 25px;
  }
}
@media (min-width: 480px) and (max-width: 769px) {
  .hero__popup {
    width: calc(100% - 100px);
  }
  .hero__popup--alert {
    left: 10%;
  }
  .hero__popup--alertInfo {
    left: 10%;
    font-size: 1.5rem;
  }
}
@media (min-width: 769px) and (max-width: 960px) {
  .hero__popup {
    width: calc(100% - 100px);
  }
  .hero__popup--alert {
    left: 10%;
  }
  .hero__popup--alertInfo {
    font-size: 1.5rem;
    left: 10%;
  }
}